const getTripFilterTemplate = (currentFilter, filteredPoints) => `<form class="trip-filters" action="#" method="get">
                    <div class="trip-filters__filter">
                      <input id="filter-everything" class="trip-filters__filter-input  visually-hidden" type="radio" name="trip-filter" value="everything" ${(currentFilter === "everything" /* FilterType.ALL */) ? 'checked' : ''} ${(filteredPoints.get("everything" /* FilterType.ALL */).length) ? '' : 'disabled'}>
                      <label class="trip-filters__filter-label" for="filter-everything">Everything</label>
                    </div>

                    <div class="trip-filters__filter">
                      <input id="filter-future" class="trip-filters__filter-input  visually-hidden" type="radio" name="trip-filter" value="future" ${(currentFilter === "future" /* FilterType.FUTURE */) ? 'checked' : ''} ${(filteredPoints.get("future" /* FilterType.FUTURE */).length) ? '' : 'disabled'}>
                      <label class="trip-filters__filter-label" for="filter-future">Future</label>
                    </div>

                    <div class="trip-filters__filter">
                      <input id="filter-present" class="trip-filters__filter-input  visually-hidden" type="radio" name="trip-filter" value="present" ${(currentFilter === "present" /* FilterType.PRESENT */) ? 'checked' : ''} ${(filteredPoints.get("present" /* FilterType.PRESENT */).length) ? '' : 'disabled'}>
                      <label class="trip-filters__filter-label" for="filter-present">Present</label>
                    </div>

                    <div class="trip-filters__filter">
                      <input id="filter-past" class="trip-filters__filter-input  visually-hidden" type="radio" name="trip-filter" value="past" ${(currentFilter === "past" /* FilterType.PAST */) ? 'checked' : ''} ${(filteredPoints.get("past" /* FilterType.PAST */).length) ? '' : 'disabled'}>
                      <label class="trip-filters__filter-label" for="filter-past">Past</label>
                    </div>

                    <button class="visually-hidden" type="submit">Accept filter</button>
                  </form>`;
export { getTripFilterTemplate };
