import { TripSortView } from '../view/trip-sort';
import { remove, render, replace } from '../framework/render';
export default class TripSortPresenter {
    #container;
    #target = null;
    #filterModel;
    #pointsModel;
    #getCurrentPoints;
    #currentSort = "sort-day" /* SortType.DAY */;
    #sortHandler;
    constructor(props) {
        this.#container = props.container;
        this.#target = null;
        this.#filterModel = props.filterModel;
        this.#pointsModel = props.pointsModel;
        this.#getCurrentPoints = props.getCurrentPoints;
        this.#sortHandler = props.sortHandler;
        this.#filterModel.addObserver(this.#handleFilterChange);
        this.#pointsModel.addObserver(this.#handlePointsModelChange);
    }
    #handlePointsModelChange = (updateType) => {
        switch (updateType) {
            case 'INIT': {
                if (this.#getCurrentPoints().length === 0) {
                    break;
                }
                this.updateTarget();
                break;
            }
            case 'MAJOR': {
                if (this.#getCurrentPoints().length === 0) {
                    this.remove();
                    this.#target = null;
                    break;
                }
                this.updateTarget();
                break;
            }
        }
    };
    #handleFilterChange = () => {
        this.#currentSort = "sort-day" /* SortType.DAY */;
    };
    #sortPoints = (value) => {
        const sortByPrice = (priceOne, priceTwo) => priceTwo.basePrice - priceOne.basePrice;
        const sortByTime = (timeOne, timeTwo) => (timeOne.dateTo.getTime() - timeOne.dateFrom.getTime()) - (timeTwo.dateTo.getTime() - timeTwo.dateFrom.getTime());
        const sortByDate = (dayOne, dayTwo) => dayOne.dateFrom.getTime() - dayTwo.dateFrom.getTime();
        const sorts = new Map([
            ["sort-price" /* SortType.PRICE */, sortByPrice],
            ["sort-day" /* SortType.DAY */, sortByDate],
            ["sort-time" /* SortType.TIME */, sortByTime]
        ]);
        return this.#getCurrentPoints().sort((a, b) => sorts.get(value)(a, b));
    };
    getSortedPoints = () => {
        const points = this.#sortPoints(this.#currentSort);
        this.updateTarget();
        return points;
    };
    updateTarget = () => {
        if (this.#target) {
            const newTarget = this.#getTarget();
            replace(newTarget, this.#target);
            this.#target = newTarget;
            return;
        }
        this.#target = this.#getTarget();
        this.render();
    };
    #updateSort = (sort) => {
        this.#currentSort = sort;
        this.#sortHandler();
    };
    #getTarget = () => new TripSortView(this.#currentSort, this.#updateSort);
    render = () => {
        render(this.#target, this.#container, 'afterbegin');
    };
    remove() {
        remove(this.#target);
    }
}
